.square {
  position: absolute;
  min-width: 30vw;
  min-height: 60vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.square span:nth-child(1) {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
  background: rgba(41, 161, 241, 0.5);
  border-radius: 39% 54% 57% 29% / 41% 47% 58% 59%;
  transition: 0.5s;
  -webkit-animation: animate 7s linear infinite;
          animation: animate 7s linear infinite;
  z-index: -1;
}

.square span:nth-child(2) {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
  background: rgba(41, 161, 241, 0.6);
  border-radius: 39% 54% 57% 29% / 41% 47% 58% 59%;
  transition: 0.5s;
  -webkit-animation: animate 9s linear infinite;
          animation: animate 9s linear infinite;
  z-index: -1;
}

.square span:nth-child(3) {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
  background: rgba(41, 161, 241, 0.8);
  border-radius: 39% 54% 47% 29% / 41% 47% 58% 59%;
  transition: 0.5s;
  -webkit-animation: animate2 10s linear infinite;
          animation: animate2 10s linear infinite;
  z-index: -1;
}

@-webkit-keyframes animate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes animate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@-webkit-keyframes animate2 {
  0% {
    transform: rotate(360deg);
  }

  100% {
    transform: rotate(0deg);
  }
}

@keyframes animate2 {
  0% {
    transform: rotate(360deg);
  }

  100% {
    transform: rotate(0deg);
  }
}

.squarePanel {
