}

.squarePanel:hover span:nth-child(1) {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #33ccff;
  border: none;
  border-radius: 20% 25% 30% 35% / 45% 50% 55% 25%;
  transition: 0.5s;
  -webkit-animation: animatePanel 8s linear infinite;
          animation: animatePanel 8s linear infinite;
  z-index: -1;
}

.squarePanel span:nth-child(2) {
  position: absolute;
  top: 50;
  left: 50;
  width: 70%;
  height: 70%;
  /* border: 1px solid black; */
  border-radius: 50% 50% 50% 50% / 50% 50% 50% 50%;
  transition: 0.5s;
  /* animation: animatePanel 6s linear infinite; */
  z-index: -1;
}

.squarePanel:hover span:nth-child(2) {
  position: absolute;
  top: 50;
  left: 50;
  width: 70%;
  height: 70%;
  background-color: #99ccff;
  border: none;
  border-radius: 20% 25% 30% 35% / 45% 50% 55% 25%;
  transition: 0.5s;
  -webkit-animation: animatePanel 6s linear infinite;
          animation: animatePanel 6s linear infinite;
