}

.squarePanel span:nth-child(3) {
  position: absolute;
  top: 50;
  left: 50;
  width: 45%;
  height: 45%;
  /* border: 1px solid black; */
  border-radius: 50% 50% 50% 50% / 50% 50% 50% 50%;
  transition: 0.5s;
